<template>
  <div>
    <div class="m-banner">
      <div class="m-banner-details">
        <span></span>
      </div>
    </div>
    <div class="container mt-5">
      <div class="row">
        <div class="col-7">
          <div class="container-fluid">
            <div class="row">
              <div class="col-6">
                <!-- img parent -->
                <div class="p-d-img-parent">
                  <img :src="product.img" alt="" srcset="" />
                </div>
              </div>
              <!-- img child -->
              <div class="col-6" v-for="img in product.listImg" :key="img.id">
                <div class="p-d-img-parent">
                  <img :src="img" alt="" srcset="" />
                </div>
              </div>

              <!-- end child -->
            </div>
          </div>
        </div>
        <div class="col-5">
          <div class="list-product-detail">
            <!-- header -->
            <div class="product-header">
              <div class="d-flex">
                <h2>{{ productName }}</h2>
                <div class="ml-2">
                  <button id="love-product" @click="favoriteProduct" v-html="!favorite ? dislike : like"></button>
                </div>
              </div>
              <div>
                <span>{{ product.productCode }} </span> |
                <span>Đã bán: {{ totalPay }} </span> |
                <span>Đánh giá: {{ totalRate }} </span>
              </div>

              <div>
                <el-rate v-model="rating" disabled show-score text-color="#ff9900" score-template="{value} / 5">
                </el-rate>
              </div>
              <div class="price-sell">
                <div v-if="product.sale != 0" class="d-flex">
                  <h5 style="color: black; text-decoration: line-through">
                    {{
                        toMoney(product.priceSell / ((100 - product.sale) / 100))
                    }}
                  </h5>

                  <h5 style="color: red">
                    - {{ toMoney(product.priceSell) }} (-{{ product.sale }})%
                  </h5>
                </div>
                <h5 v-if="product.sale == 0" style="color: black">
                  {{ toMoney(product.priceSell) }}
                </h5>
              </div>
              <hr />
            </div>
            <!-- header -->
            <!-- content -->
            <div :class="checkValidate ? 'product-content' : ''">
              <!-- color -->
              <div class="product-color">
                <div class="title">
                  <h5>Màu</h5>
                </div>
                <div class="d-flex">
                  <div class="content-color" v-for="c in listColor" :key="c.id">
                    <div class="color">
                      <input type="radio" name="check-color" :id="'color-' + c.id" :value="c.id" @click="chooseColor"
                        hidden />
                      <label :for="'color-' + c.id" class="check-color" :data-id="c.id">
                        {{ c.description }}
                      </label>
                    </div>
                  </div>
                </div>
              </div>
              <!-- size -->
              <div class="product-color">
                <div class="title">
                  <h5>Kích cỡ</h5>
                </div>
                <div class="d-flex">
                  <div class="content-size d-flex" v-for="s in listSize" :key="s.id">
                    <div class="size">
                      <input type="radio" name="check-size" :id="'size-' + s.id" :value="s.id" @click="chooseSize"
                        hidden />
                      <label :for="'size-' + s.id" class="check-size">
                        {{ s.description }}
                      </label>
                    </div>
                  </div>
                </div>
              </div>
              <!-- brand -->
              <div class="product-color">
                <div class="title">
                  <h5>Thương hiệu</h5>
                </div>
                <div class="d-flex">
                  <div class="content-size d-flex">
                    <div class="size">
                      <label class="check-size">
                        {{ brandName }}
                      </label>
                    </div>
                  </div>
                </div>
              </div>
              <!-- quantily -->
              <div class="product-color">
                <div class="title">
                  <h5>Số lượng</h5>
                </div>
                <div class="product-quantity">
                  <template>
                    <el-input-number size:large v-model="cart.quantity" @change="handleQuantity" :min="1"
                      :max="product.quantity"></el-input-number>
                    <div>
                      <p>
                        <span>{{ product.quantity }}</span> sản phẩm có sẵn
                      </p>
                    </div>
                  </template>
                </div>
              </div>
              <div v-if="checkValidate" class="messager">
                Vui lòng chọn đủ thông tin
              </div>
            </div>
            <hr />
            <!-- content -->
            <!-- cart -->
            <div class="product-add-cart">
              <div class="choose-size">
                <a @click="chooseModalSize = true"
                  style="border-bottom: 1px solid rgba(120, 115, 115, 0.414); cursor: pointer; font-size: 15px;">
                  Hướng dẫn chọn size
                </a>
              </div>
              <div class="info-other">
                <div class="mt-4">
                  <font-awesome-icon icon="fa-solid fa-check" /> Miễn phí vận
                  chuyển Cho đơn hàng
                </div>
                <div>
                  <font-awesome-icon icon="fa-solid fa-check" /> Đổi trả miễn
                  phí trong vòng 30 ngày kể từ ngày mua
                </div>
              </div>
              <div class="btn-add-cart">
                <div>
                  <el-button type="primary" plain @click="addCart">Thêm vào giỏ hàng</el-button>
                </div>
                <div>
                  <el-button type="primary" plain @click="payNow">Mua ngay</el-button>
                </div>
                <div>
                  <el-button type="primary" plain>Quay lại</el-button>
                </div>
              </div>
            </div>
            <!-- cart -->
            <div class="foot">
              <div class="foot-detail">
                <el-collapse>
                  <el-collapse-item title="Giới thiệu" name="1">
                    <div v-html="description"></div>
                  </el-collapse-item>
                  <el-collapse-item title="Mô tả" name="2">
                    <div v-html="content"></div>
                  </el-collapse-item>
                  <el-collapse-item title="Cách sử dụng" name="3">
                    <div>
                      Giặt máy ở chế độ nhẹ, nhiệt độ thường. Không sử dụng hóa
                      chất tẩy có chứa Clo. Phơi trong bóng mát. Sấy thùng, chế
                      độ nhẹ nhàng. Là ở nhiệt độ trung bình 150 độ C. Giặt với
                      sản phẩm cùng màu. Không là lên chi tiết trang trí.
                    </div>
                  </el-collapse-item>
                </el-collapse>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div>
      <Comment :productId="productId" />
    </div>
    <el-dialog top="24vh" title="Hướng dẫn chọn size" :visible.sync="chooseModalSize" width="60%" height="100vh">
      <div class="container text-center">
        <img src="@/assets/img/change-size.png" alt="" srcset="" style="object-fit: contain" width="100%" />
      </div>

    </el-dialog>
  </div>
</template>

<script>
import {
  getProductDetailById,
  getProductDetailBySpe,
} from "@/service/user/product-detail";
import Comment from "@/views/user/product/ProductCommentView.vue";
import { addCart } from "@/service/user/cart";
export default {
  props: ["productId"],
  components: {
    Comment,
  },
  data() {
    return {
      productName: "",
      product: {},
      listColor: [],
      listSize: [],
      choose: {
        productId: this.productId,
        sizeId: null,
        colorId: null,
      },
      quantily: 1,
      checkValidate: false,
      cart: {
        productId: null,
        quantity: 1,
        totalQuantity: null,
      },
      favorite: false,
      parentId: null,
      dislike: '<i class="far fa-heart"></i>',
      like: '<i class="fas fa-heart"></i>',
      totalRate: 0,
      rating: 0,
      totalPay: 0,
      chooseModalSize: false,
      brandName: "",
      content: "",
      description: ""
    };
  },
  created() {
    window.scrollTo(0, 0);
    getProductDetailById(this.productId).then((res) => {
      console.log(res)
      this.totalRate = res.totalRate;
      this.rating = res.rate;
      this.totalPay = res.totalPay;
      this.product = res;
      this.listColor = res.color;
      this.listSize = res.size;
      this.productName = res.productName;

      this.parentId = res.id;
      this.favorite = res.favorite;
      this.brandName = res.brandName
      this.content = res.content
      this.description = res.description
    });
  },
  methods: {
    // size
    chooseSize(e) {
      this.cart.quantity = 1;
      this.checkValidate = false;
      let checkedInput = e.path[0].checked ? 1 : 0;

      if (checkedInput == 1) {
        let labelActive = e.path[1].childNodes[1];
        // get id cateid
        let listLabel = document.querySelectorAll(".check-size");
        listLabel.forEach((e) => {
          e.classList.remove("active");
        });
        labelActive.classList.add("active");

        let sizeId = e.path[0].value;
        this.choose.sizeId = sizeId;
        this.afterClickColorAndSize();
      }
    },
    // color
    chooseColor(e) {
      this.cart.quantity = 1;
      this.checkValidate = false;
      let checkedInput = e.path[0].checked ? 1 : 0;

      if (checkedInput == 1) {
        let labelActive = e.path[1].childNodes[1];

        let listLabel = document.querySelectorAll(".check-color");
        listLabel.forEach((e) => {
          e.classList.remove("active");
        });
        labelActive.classList.add("active");

        let colorId = e.path[0].value;

        this.choose.colorId = colorId;

        if (this.choose.sizeId != null) {
          this.choose.sizeId = null;
        }

        this.afterClickColorAndSize();
      }
    },
    afterClickColorAndSize() {
      let data = JSON.stringify(this.choose);
      getProductDetailBySpe(data)
        .then((res) => {
          this.product = res[0];
          this.checkQuantity(res);
        })
        .catch((err) => {
          console.log(err.response.data);
        });
    },
    // disable size 0 quantity
    checkQuantity(productList) {
      if (this.choose.colorId != null && this.choose.sizeId == null) {
        // xoa active size
        let listLabelSize = document.querySelectorAll(".check-size");
        listLabelSize.forEach((e) => {
          e.classList.remove("active");
        });
        productList.forEach((e) => {
          let sizeId = "size-" + e.size.id;
          let inputSize = document.getElementById(sizeId);
          inputSize.checked = false;
          if (e.quantity == 0) {
            inputSize.disabled = true;

            let labelSize = inputSize.parentElement.childNodes[1];
            labelSize.classList.add("inactive");
          } else {
            inputSize.disabled = false;
            let labelSize = inputSize.parentElement.childNodes[1];
            labelSize.classList.remove("inactive");
          }
        });
      } else {
        this.cart.productId = productList[0].id;
        this.cart.totalQuantity = productList[0].quantity;
      }
    },
    // add cart
    addCart() {
      if (this.choose.colorId == null || this.choose.sizeId == null) {
        this.checkValidate = true;
      } else {
        // nếu đang đang nhập
        if (this.$store.getters.CURRENT_USER) {
          let data = JSON.stringify(this.cart);
          addCart(data)
            .then((res) => {
              console.log(res);
              this.$root.$refs.userHeader.totalItemByCart();
            })
            .catch((err) => {
              console.log(err.response.data);
            });
        } else {
          this.$store.commit("SET_CART", this.cart);
          this.$root.$refs.userHeader.totalItemByCart();
        }

        this.$swal(
          "Thành công",
          "Thêm vào giỏ hàng thành công",
          "success"
        ).then(function () { });
      }
    },
    payNow() {
      if (this.choose.colorId == null || this.choose.sizeId == null) {
        this.checkValidate = true;
      } else {
        this.$router.push(`/dat-hang-ngay/san-pham/${this.cart.productId}/so-luong/${this.cart.quantity}`);
      }
    },
    handleQuantity(value) {
      console.log(value);
    },
    // covert tien
    toMoney(totalprice) {
      var formatter = new Intl.NumberFormat("it-IT", {
        style: "currency",
        currency: "VND",
      });
      return formatter.format(totalprice);
    },
    async favoriteProduct() {
      let value = this.$root.$refs.userHeader.favoriteProductHeader(
        this.parentId
      );
      value.then((res) => {
        this.favorite = res == 1 ? false : true;
      });
    },
  },
};
</script>

<style lang="scss" scoped>
@import url("@/assets/user/css/product-detail");
</style>
